import {mapActions} from 'vuex'


export default {
  data(){
    return {
      username:'',
      password:''
    }
  },
  methods:{
    ...mapActions(['login']),

    onLogin:function(){
      this.login({username: this.username,password: this.password})
        .then(()=>{
          this.$router.push({path:this.$route.query.redirect || '/'})
        })
      console.log(this.username + " " + this.password)
    }
  }
}


